body.settings_page_werstreamtes {

  background: #DFAD05;

  .components-snackbar-list {
    right: 20px;
    top: 10px;
    width: fit-content;
  }

  #widget-werstreamtes-settings {
    max-width: 20%;
    margin: 15% auto 0;

    button {
      border: unset;
      background: #A03C21;
    }

    input {
      border-radius: 5px;
      font-size: 16px;
    }

    .wse-plugin__title {
      text-align: center;
      margin-bottom: 10px;

      img {
        height: 70px;
      }

    }

    label {
      text-transform: unset;
      font-size: 15px;
      display: block;
      color: #302B25;
    }

    small {
      margin-top: 50px;
      display: block;
      text-align: center;

      a {
        color: #A03C21;
      }

    }

    p {
      color: #000;
    }

  }

}
